import Map from 'ol/Map.js';
import View from 'ol/View.js';
import TileLayer from 'ol/layer/Tile.js';
import OSM from 'ol/source/OSM.js';
import StadiaMaps from 'ol/source/StadiaMaps.js';
import GeoJSON from 'ol/format/GeoJSON.js';
import VectorLayer from 'ol/layer/Vector.js';
import VectorSource from 'ol/source/Vector.js';
import {useGeographic} from 'ol/proj.js';
import Fill from 'ol/style/Fill.js';
import Stroke from 'ol/style/Stroke.js';
import Style from 'ol/style/Style.js';
import RegularShape from 'ol/style/RegularShape.js';
import Text from 'ol/style/Text.js'

import LayerSwitcher from 'ol-ext/control/LayerSwitcher'

// ol-ext
import "ol/ol.css"
import "ol-ext/dist/ol-ext.css"
import Legend from 'ol-ext/legend/legend.js'
import ControlLegend from 'ol-ext/control/legend.js'


useGeographic();

const color1 = '#edf8fb';
const color2 = '#b3cde3';
const color3 = '#8c96c6';
const color4 = '#8856a7';
const color5 = '#810f7c';


// layer definition
const StadiaDark = new TileLayer({
      source: new StadiaMaps({
        layer: 'alidade_smooth_dark',
        retina: true,
        // apiKey: 'OPTIONAL'
      }),
    });


// style definition
var style = new Style({
    stroke: new Stroke({color: 'gray', width: 0.5}),
    fill: new Fill()
});

var pente_batiment = function(feature) {
    var value = feature.get('pente');
    var color = value <=  15 ? color1 :
                value <=  30 ? color2 :
                value <=  45 ? color3 :
                value <=  60 ? color4 : color5;
    style.getFill().setColor(color);
    return style;
};

/*
const iris = new VectorLayer({
	renderMode: 'image',
	source: new VectorSource({
		url: './geojson/contours_iris.geojson',
		format: new GeoJSON(),
		projection: 'EPSG:4326'
		}),
	});
*/

const batiment = new VectorLayer({
	renderMode: 'image',
	source: new VectorSource({
		url: './geojson/batiment.geojson',
		format: new GeoJSON(),
		projection: 'EPSG:4326'
		}),
	style: pente_batiment,
	});

// map definition

const map = new Map({
	layers: [StadiaDark,batiment],

		// map attributes
		target: 'map',
		view: new View({
			center: [0.6873,47.394],
			zoom: 17,
		}),
});

const opacityInput = document.getElementById('opacity-input');
const opacityOutput = document.getElementById('opacity-output');
function update() {
  const opacity = parseFloat(opacityInput.value);
  // layer opacity mod
  batiment.setOpacity(opacity);
  opacityOutput.innerText = opacity.toFixed(2);
}
opacityInput.addEventListener('input', update);
update();

 // Define a new legend
  var legend = new Legend({
    title: 'Pente de toiture',
    margin: 5,
    maxWidth: 300,
	size: [20, 20],
	titleStyle: new Text({font: 'bold 12px sans-serif'}),
	textStyle: new Text({font: '12px sans-serif'})
  });

  var legendCtrl = new ControlLegend({
    legend: legend,
    collapsed: false
  });
  map.addControl(legendCtrl);

legend.addItem({
  title: '0 - 15°',
  typeGeom: 'Polygon',
  style: new Style({
	  fill: new Fill({ color: color1})
  })

});legend.addItem({
  title: '15 - 30°',
  typeGeom: 'Polygon',
  style: new Style({
	  fill: new Fill({ color: color2})
  })

});legend.addItem({
  title: '30 - 45°',
  typeGeom: 'Polygon',
  style: new Style({
	  fill: new Fill({ color: color3})
  })

});legend.addItem({
  title: '45 - 60',
  typeGeom: 'Polygon',
  style: new Style({
	  fill: new Fill({ color: color4})
  })
});

legend.addItem({
  title: '60° et +',
  typeGeom: 'Polygon',
  style: new Style({
	  fill: new Fill({ color: color5})
  })
});
